import React from 'react'

import { useDispatch, useSelector } from 'react-redux'

import { obtenerPokemonesAccion, siguientePokemonesAccion, anteriorPokemonesAccion, detallePokemonAccion } from '../redux/pokeDuck'
import DetallePokemon from './DetallePokemon'


const Pokemoness = () => {

    const dispatch = useDispatch()

    const pokeList = useSelector(store => store.pokemones.results)
    const next = useSelector(store => store.pokemones.next)
    const previous = useSelector(store => store.pokemones.previous)


    return (
        <div className="container">
            <div className="row">
               
                <div className="col-md-6 col-sm-12 mb-3">
                <h3 className="text-uppercase">lista de pokemones</h3>

                    <div className="btn-group-lg" role="group" aria-label="Basic example">
                        {
                            pokeList.length === 0 &&
                            <button
                                className="btn btn-primary mt-3"
                                onClick={() => { dispatch(obtenerPokemonesAccion()) }}>
                                Mostrar pokemones
                                </button>
                        }
                        {
                            previous &&
                            <button
                                className="btn btn-danger mt-3 mx-2"

                                onClick={() => { dispatch(anteriorPokemonesAccion()) }}>
                                Anterior
                            </button>
                        }
                        {
                            next &&
                            <button
                                className="btn btn-primary mt-3 mx-2"
                                onClick={() => { dispatch(siguientePokemonesAccion())   }   }>
                                Siguiente
                            </button>
                        }
                    </div>

                    <ul className="list-group list-group-flush">
                        {
                            pokeList.map((item) => (
                                <li
                                    key={item.name}
                                    className="list-group-item text-uppercase text-left">
                                        {item.name} 

                                    <button 
                                        className="btn btn-secondary float-right"
                                        onClick={() =>{dispatch(detallePokemonAccion(item.url)) }    }>
                                        Detalle
                                    </button>
                                 </li>
                            )
                            )
                        }
                    </ul>

                </div>

                <div className="col-sm-12 col-md-6">
                    <DetallePokemon/>
                </div>

            </div>







        </div>
    )
}

export default Pokemoness
