// Inportaciones
import axios from 'axios'



// Constant - Constantes

const dataInicial = {
    count:0,
    next:null,
    previous:null,
    results:[]
}

// Type
const OBTENER_POKEMONES_EXITO = "OBTENER_POKEMONES_EXITO";
const SIGUENTE_POKEMONES_EXITO = "SIGUENTE_POKEMONES_EXITO" ;
const ANTERIOR_POKEMONES_EXITO = "ANTERIOR_POKEMONES_EXITO"
const DETALLE_POKEMONES_EXITO = "DETALLE_POKEMONES_EXITO"


// Reduce - Reducir
export default function pokeReducer(state = dataInicial, action){
    switch(action.type){
        case OBTENER_POKEMONES_EXITO:
            return { ...state, ...action.payload }

        case SIGUENTE_POKEMONES_EXITO:
                return { ...state,...action.payload}

        case ANTERIOR_POKEMONES_EXITO:
                return { ...state,...action.payload}

        case DETALLE_POKEMONES_EXITO:
                return { ...state,detallePoke: action.payload}
    
    

        default:
            return state
    }
}


// -------------- Actions - Acciones -------------- 

// Obtener detalle pokemon

export const detallePokemonAccion = (url ="https://pokeapi.co/api/v2/pokemon/1/") => async (dispatch) =>{

 

    try {
        const res = await axios.get(url)
        dispatch({            
            type:DETALLE_POKEMONES_EXITO,
            payload:{
                nombre:res.data.name,
                ancho:res.data.weight,
                alto:res.data.height,
                foto:res.data.sprites.front_default,
                habilidadUno:res.data.abilities[0].ability.name,
                habilidadDos:res.data.abilities[1].ability.name

            }
        }
        )
    } catch (error) {
        console.log(error)
    }
}




// Obtener datos
export const obtenerPokemonesAccion = () => async ( dispatch) =>{

    if(localStorage.getItem('offset=0')){
        console.log("del local")
        dispatch({
            type:OBTENER_POKEMONES_EXITO,
            payload:JSON.parse(localStorage.getItem('offset=0'))
        })
        return 
    }

    try {
        const res = await axios.get(`https://pokeapi.co/api/v2/pokemon?offset=0&limit=20`)
        dispatch({
            type:OBTENER_POKEMONES_EXITO,
            payload:res.data
        })
        localStorage.setItem('offset=0',JSON.stringify(res.data))
    } catch (error) {
        console.log(error)
    }
} 

// Siguiente
export const siguientePokemonesAccion = () => async (dispatch,getState) =>{

    const {next} = getState().pokemones 

    // Si obtenemos datos del localStorage entra al if
    if(localStorage.getItem(next)){
        dispatch({
            type:SIGUENTE_POKEMONES_EXITO,
            payload:JSON.parse(localStorage.getItem(next))
        })
        return
    }

    try {
      
        const res = await axios.get(next)
        dispatch({
            type:SIGUENTE_POKEMONES_EXITO,
            payload:res.data
        })
        localStorage.setItem(next,JSON.stringify(res.data))

    } catch (error) {
        console.log(error)
    }
}

// Anterior
export const anteriorPokemonesAccion = () => async (dispatch,getState) =>{
  
    const {previous} = getState().pokemones

    // Si obtenemos datos del localStorage entra al if
    if(localStorage.getItem(previous)){
        dispatch({
            type:SIGUENTE_POKEMONES_EXITO,
            payload:JSON.parse(localStorage.getItem(previous))
        })
        return
    }

    try {
        const res =await axios.get(previous)
        dispatch({
            type:ANTERIOR_POKEMONES_EXITO,
            payload:res.data
        })

        localStorage.setItem(previous,JSON.stringify(res.data))

    } catch (error) {
        console.log(error)
    }
}