import React,{Fragment} from 'react'

import {useDispatch,useSelector} from 'react-redux'
import {detallePokemonAccion} from '../redux/pokeDuck'

const DetallePokemon = () => {

    const dispach = useDispatch()
    const pokemon = useSelector(store => store.pokemones.detallePoke)
    
    React.useEffect(()=>{
        const obtenerData = () =>{
            dispach(detallePokemonAccion())
        }       
        obtenerData()
    },[dispach])


    return (
        pokemon ?(
        <div className="container card"> 
            <h3 className="mt-3">Detalle pokemon</h3>
            <div className="card-body">
                <img src={pokemon.foto} className="img-fluid" alt=""/>
                <div className="card-title">
                    <h4 className="text-uppercase"> {pokemon.nombre}</h4>
                </div>
                <p className="card-text text-uppercase text-left">
                    <strong>Ancho:</strong> {pokemon.ancho} <br/>
                    <strong>Alto:</strong>{pokemon.alto*10} Cm.<br/>
                    <strong>Habilidad 1:</strong> {pokemon.habilidadUno} <br/>
                    <strong>Habilidad 2:</strong> {pokemon.habilidadDos} <br/>
                  
                
                </p>
             </div>
        </div>)
        :null
    )
}

export default DetallePokemon
